import React, { Component } from 'react'

interface customProps {
    submitHandler: (event: React.FormEvent) => void
}

export class SearchBox extends Component<customProps> {

    constructor(props: customProps) {
        super(props)
    }
    render() {
        return (
            <div className="searchBox flexContainer">
                <div className="searchContainer" >

                    <h2>Search for: </h2>
                    <div>
                        <form action="submit" defaultValue="ime" onSubmit={this.props.submitHandler} >
                            <input placeholder="Search Word" autoComplete="off" type="search" name="wordSearch" />
                            <input className="submitButton" type="submit" value="Search" />
                        </form>

                    </div>
                </div>
            </div>
        )
    }
}

export default SearchBox
