import React, { Component } from 'react'
import SynonymWord from '../models/Synonym'


interface customProps {
    searchWord: string,
    searchList: SynonymWord[],
    canUpdate: boolean,
    submitHandler: (event: React.FormEvent) => void
}


export class SynonymList extends Component<customProps> {

    constructor(props: customProps) {
        super(props)
    }

    render() {
        return (
            <div className="synonyms flexContainer">
                <div>
                    <h3>Synonyms for: {this.props.searchWord || '(Use search)'}</h3>
                    <div>
                        {this.props.canUpdate ?
                            <div className="wordTagContainer clearfix">
                                {
                                    this.props.searchList.length ?
                                        this.props.searchList.map((item) => {
                                            return <span key={item.word} className="wordTag">{item.word}</span>
                                        })
                                        : <div style={{ textAlign: "center", padding: "20px", color: "#777" }}>No synonyms found</div>
                                }
                            </div> : <div> item can not be assigned </div>
                        }
                        {
                            this.props.searchWord ?
                                <form action="submit" onSubmit={this.props.submitHandler} >
                                    <input placeholder={'Synonyms for: ' + this.props.searchWord} type="text" name="wordAdd" id="addword" />
                                    <input className="submitButton" type="submit" value="Add" />
                                </form>
                                : <div></div>
                        }

                    </div>

                </div>
            </div>
        )
    }
}

export default SynonymList
