import React, { Component } from 'react';
import './App.css';

import { SearchBox } from './components/SearchBox';
import { SynonymList } from './components/SynonymList';

import { SynonymWord } from "./models/Synonym";

interface State {
  searchList: SynonymWord[],
  wordList: SynonymWord[],
  searchWord: string,
  activeSearchWord: string,
  canUpdateList: boolean,
}


class App extends Component<{}, State> {


  constructor(props: any) {
    super(props)

    this.state = {
      canUpdateList: true,
      searchList: [],
      wordList: [
        { word: 'one', synonymGroup: 3 },
        { word: 'two', synonymGroup: 1 },
        { word: 'three', synonymGroup: 2 },
        { word: 'four', synonymGroup: 1 },
        { word: 'five', synonymGroup: 3 },
        { word: 'six', synonymGroup: 3 },
        { word: 'very very logn word', synonymGroup: 2}
      ],
      searchWord: '',
      activeSearchWord: ''
    }
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  private handleSubmit = (e: any) => {
    e.preventDefault();
    if (e.target.wordSearch) {
      let word = e.target.wordSearch.value.toLowerCase()
      if (word) this.findWord(word)
    } else {
      this.addWord(e.target.wordAdd.value.toLowerCase())
    }
  }

  private findWord = (word: string, update:boolean = true) => {
    let found = this.state.wordList.find(item => item.word === word)    
    this.setState({ searchWord: word })

    if (found) {
      this.setState({
        searchList: this.state.wordList.filter(item => item.synonymGroup === found.synonymGroup)
      })
      return found
    } else {
      this.setState({ searchList: [] })
    }
    return null
  }

  private addWord = (word: any) => {
    let id = this.state.wordList.length

    //if word already belongs to some group
    if(this.findWord(word, false)) return () => {this.setState({canUpdateList:false})}

    //if main word already has synonym
    let mainWord = this.findWord(this.state.searchWord)
    if(mainWord) return this.assignToGroup(word, mainWord.synonymGroup)

    //if main word has no synonyms
    this.assignToGroup(this.state.searchWord, id)
    this.assignToGroup(word, id)
  }

  private assignToGroup = (word: any, group: number) => {
    this.setState({canUpdateList: true})
    let tempState = this.state.wordList
    tempState.push({word:word, synonymGroup: group})
    this.setState({wordList: tempState})

    //update synonym list and search word
    this.findWord(word)
  }

  render() {
    return (
      <div className="SyApp" >
        <SearchBox submitHandler={this.handleSubmit} />
        <SynonymList  submitHandler={this.handleSubmit} 
                      searchList={this.state.searchList} 
                      searchWord={this.state.searchWord}
                      canUpdate={this.state.canUpdateList} />
      </div>
    );
  }
}
export default App;
